/* GLOBALES */

/* HEAD */

@import "layout/footer";
@import "components/hero";
@import "pages/home.scss";

* {
  font-family: "Open Sans", sans-serif;
  margin: 0;
  box-sizing: border-box;
  /* font-family: 'Rubik', sans-serif;*/ /*Cogerla para los títulos, deb. fort., menu (nav), hero y footer*/
}
.title {
  color: #099d8d;
  text-align: center;
  margin: 60px 0 45px 0;
  font-family: "Rubik", "sans-serif";
  font-size: 24px;
}

/*Header*/
.header__menu {
  display: flex;
  justify-content: space-between;
  position: fixed;
  top: 0px;
  width: 100%;
  height: 60px;
  background-color: #099d8d;
  z-index: 999;
  opacity: 0.9;
}
.header__menu__items {
  display: inline;
  margin: 10px;
  font-weight: bold;
  font-family: "Rubik-Medium", "sans-serif";
  font-size: 15px;
}
.logo {
  margin: 5px 0 0 10px;
}
.header__menu__nav {
  position: relative;
  top: 20px;
}
.link {
  color: #a1ca9b;
  text-decoration: none;
}
.link:hover {
  color: #eb636b;
}

