@media all and (min-width: 768px) and (max-width: 1200px) {
  .hero-section {
    height: 361px;
  }
  .hero-container .hero-text {
    max-width: 441px;
  }
}

@media all and (max-width: 768px) {
  .hero-section {
    height: 316px;
  }
  .hero-container .hero-text {
    width: calc(100% - 30px);
    margin: auto 15px;
    -webkit-transform: translateY(50%);
    transform: translateY(50%);
    background-color: rgba(255, 255, 255, 1);
  }
  .hero-container .hero-text h2 {
    margin: 15px 15px;
  }
}

/*Team*/

.team {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  margin: 50px;
}
.team-description {
  text-align: center;
}
@media all and (min-width: 768px) {
  .team {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    margin: 50px 100px;
  }
}

/*Fortalezas y debilidades*/

.sw__detail {
  box-sizing: border-box;
  /* border: 1px solid black; */
  background-color: white;
  width: 27px;
  height: 27px;
  margin: auto;
  display: block;
  position: relative;
  transform: translateY(50%) /* translateX(465px) */ rotate(45deg);
  z-index: 980;
}
.central-container__sw {
  /*border: 1px solid black;*/
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  width: 100%;
  background-color: #f1f1f1;
}
.strengths {
  /* border: 1px solid black; */
  width: 40%;
  padding: 45px 0 60px 0;
}
.weakness {
  width: 40%;
  padding: 45px 0 60px 0;
}
.strenghts__title {
  color: #099d8d;
  text-align: center;
}
.weakness__title {
  color: #099d8d;
  text-align: center;
}
.strengths__text {
  text-align: center;
}
.weakness__text {
  text-align: center;
}

/*mobile*/
@media all and (max-width: 767px) {
  .central-container__sw {
    flex-direction: column;
    align-items: center;
  }
  .strengths {
    /* height: 350px; */
    width: 300px;
    padding: 30px 0 30px 0;
  }
  .weakness {
    /* height: 350px; */
    width: 300px;
    padding-bottom: 45px;
  }
}

/*Quiénes somos*/

.article__container {
  text-align: center;
  width: 80%;
  margin: auto;
}
.img__personal {
  width: 180px;
  height: 180px;
  background-size: contain;
  border-radius: 10px;
  border: 5px solid grey;
  margin: 30px 0 15px 0;
}
.h3__name {
  margin: 0 0 15px 0;
}
.article__paragraph {
  color: #54585a;
}

.icons__link {
  color: #099d8d;
}
a.icons__link:hover {
  color: #fabada;
}

.fab {
  padding: 15px 5px;
}
.fas {
  padding: 15px 5px;
}
.dot {
  height: 3px;
  width: 3px;
  background-color: black;
  border-radius: 50%;
  display: inline-block;
  margin-bottom: 3px;
}

@media all and (min-width: 768px) {
  .article__container {
    border: 2px solid black;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
  }
  .reverse__article {
    display: flex;
    flex-direction: row-reverse;
  }
  .article__bio {
    border: 2 px solid red;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 35px 0px 0px 30px;
  }
  .reverse__bio {
    align-items: flex-end;
    padding: 35px 30px 0px 0px;
  }

  .article__paragraph {
    text-align: left;
  }
  .reverse__paragraph {
    text-align: right;
  }
  .img__personal {
    width: 205px;
    height: 205px;
  }
  .fab {
    padding: 15px 5px 0px 0px;
  }
  .fas {
    padding: 15px 5px 0px 0px;
  }
}

@media all and (min-width: 1200px) {
  .img__personal {
    width: 278px;
    height: 278px;
  }
  .article__paragraph {
    max-width: 439px;
  }
  .reverse__paragraph {
    max-width: 439px;
  }
}

