.team {
    display: flex;
	flex-direction: column;
	flex-wrap: wrap;       
	align-items: center;
    margin: 50px;  

    @media all and (min-width: 768px) {
        display: flex;
	  flex-direction: column;
	  flex-wrap: wrap;       
	  align-items: center;
	  margin: 50px 100px;  
    }

    &__description {
        text-align: center; 
    }
}
.sw{
    &__detail{
        box-sizing: border-box;
	background-color: white;
	width: 27px;
	height: 27px;
	margin: auto;
	display: block;
	position: relative;
	transform: 
	translateY(50%) 
	rotate(45deg);
	z-index: 980;
    }
    &__container{
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        position:relative;
        width: 100%;
        background-color: #f1f1f1;
        text-align: center;

        @media all and (min-width: 768px){
            flex-direction: row;
            justify-content:space-around;
        }

        &__strengthsweakness{
            width: 300px;
            padding:30px 0 30px 0;
            @media all and (min-width: 768px){
                width: 40%;
                 padding: 45px 0 60px 0;
            }
            
            &__title{
                color: #099D8D;
                text-align: center;

            
                
            }
            
    
             
    
            }
        }
    }
